:root {
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 400;

  color-scheme: light dark;
  color: rgba(255, 255, 255, 0.87);
  background-color: #242424;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  display: flex;
  place-items: center;
  min-width: 320px;
  min-height: calc(100vh + 15px);
}

h1 {
  font-size: 3.2em;
  line-height: 1.1;
}

#app {
  max-width: 1280px;
  margin: 0 auto;
  padding: 2rem;
}

button {
  border-radius: 8px;
  border: 1px solid transparent;
  padding: 0.6em 1.2em;
  font-size: 1em;
  font-weight: 500;
  font-family: inherit;
  background-color: #1a1a1a;
  cursor: pointer;
  transition: border-color 0.25s;
  color: white;
}
button:hover {
  border-color: #646cff;
}
/* button:focus, */
button:focus-visible {
  outline: 4px auto -webkit-focus-ring-color;
}
.buttons-group {
  margin: 2rem 0px;
}

.hidden-link {
  opacity: 0;
  visibility: hidden;
}
.cropped-container {
  position: relative;
  border: 2px solid #063970;
  display: flex;
  justify-content: center;
  padding: 1rem;
}
#image-cropped {
  position: absolute;
  top: 50%;
  left: 50%;
  border: 4px solid #51749b;
  transform: translate(-50%, -50%);
}

.error-message {
  color: crimson;
font-size: 1.5rem;
}

img{
  display: block;
  max-width: 100%;
}
@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #ffffff;
  }
  a:hover {
    color: #747bff;
  }
  button {
    background-color: #f9f9f9;
    color: #213547;
  }
}

